import './about.scss';
import image from './../../../assets/imgs/Bodel-1.webp'

export default function About({refAbout}) {
  return (
    <div id="Quem-Somos" ref={refAbout}>
        <h1 id="About-Title">Quem Somos</h1>
        <div className="About-Content">
            <img src={image} alt="Bodel" className="About-Image"/>
            <p className="About-Text">Somos uma produtora digital focada em realizar sonhos. Desde a elaborao do briefing at a estratgia de marketing, a DVargas pode lhe atender! Nossa equipe composta por 27 profissionais dentre roteiristas, desenvolvedores das mais diversas tecnologias, gestores de trfego e videomakers acompanhar sua ideia desde a gnese ao sucesso com louvor!  </p>
        </div>
    </div>
  )
}
