import "./packsPages.scss"

import Header from "../components/header/Header"

import {Helmet} from 'react-helmet-async'
import ReactPlayer from "react-player";
import video from './../assets/videos/video3.mp4'

export default function PacksBasicPage({refHeader}) {
  return (
    <div className="PacksPages">

      <Helmet>
        <title>Pacote Supremo</title>
        <meta name="description" content="Com o pacote Supremo no s ns desenvolveremos o seu projeto, mas o ajudaremos a elabora-lo! Alm de treinar voc para geri-lo..."/>
        <link rel="canonical" href="/Pacote-Supremo" />
      </Helmet>

      <Header title="Nossos Pacotes" refHeader={refHeader}/>
      <h1>Pacote Supremo</h1>
      <div className="PacksPages-Content">
        <ReactPlayer url={video} playing={false} width="80%" height="80%" pip={true} controls={true} className="PacksPages-Video"/>

        <h2>Legenda</h2>

        <p>Se voc gostou dessa ideia ento j sabemos que deseja muito ter um negocio de sucesso no mundo digital, mas no sabe nem por onde comear ou talvez no tenha nem a habilidade para isso, mas no se preocupe, seus problemas acabaram! Com o pacote Supremo no s ns desenvolveremos o seu projeto, mas o ajudaremos a elabora-lo! Alm de treinar voc para geri-lo aps o lanamento ainda atuando com o suporte de Marketing que voc precisa! <br /><br /> A ideia desse pacote  transformar voc num profissional de primeira linha e ligar todo o seu projeto com a habilidade necessria que voc precisara para tocar o barco quando j estiver em andamento.<br /><br /> Ok, mas como funciona?<br /><br />Sero feitas a princpio as consultas necessrias para a analise adequada do nicho pretendido ao seu projeto, aps a sua aprovao o desenvolvimento  iniciado paralelamente enquanto uma equipe de tutores lhe ensinaro o necessrio para compreender e gerir tudo o que estar sendo feito, alm de novamente aps os 2 primeiros meses de projetos ainda estarem sendo acompanhados para a postura do Marketing que vai levar o seu sonho as estrelas! <br /><br />Gostou desse pacote? Entre em contato pelo boto abaixo, aguardo voc daqui a pouco!</p>
        <a href="/#Contato"><h4 className="PacksPages-Button" >Contato</h4></a>
      </div>  
    </div>
  )
}
