import './packs.scss';

import { useState } from "react";
import { Link } from 'react-router-dom';

import basicoIcon from './../../../assets/svgs/Basico-Icon.svg'
import avancadoIcon from './../../../assets/svgs/Avancado-Icon.svg'
import supremoIcon from './../../../assets/svgs/Supremo-Icon.svg'
import basicoIconW from './../../../assets/svgs/Basico-Icon-W.svg'
import avancadoIconW from './../../../assets/svgs/Avancado-Icon-W.svg'
import supremoIconW from './../../../assets/svgs/Supremo-Icon-W.svg'

export default function Packs({refPacks}) {

  const [sliderPosition,SetSliderPosition ] = useState("Packs-1");

  return (
    <div id="Pacotes" >

      <div ref={refPacks}>
        <h1 >Pacotes</h1>
        <div className="Packs-Start" >
          <div>
            <img src={basicoIcon} alt="basico icon" />
            <h4 id="Packs-Basic" className="Packs-Button" onClick={()=>SetSliderPosition("Packs-1")}>Bsico</h4>
          </div>
          <div>
            <img src={avancadoIcon} alt="avancado icon" />
            <h4 id="Packs-Advanced" className="Packs-Button" onClick={()=>SetSliderPosition("Packs-2")}>Avanado</h4>
            </div>
          <div>
            <img src={supremoIcon} alt="supremo icon" />
            <h4 id="Packs-Supreme" className="Packs-Button" onClick={()=>SetSliderPosition("Packs-3")}>Supremo</h4>
            </div>
        </div>
      </div>
      <div className="Packs-Carousel">
        <div id="Packs-Inner" className={sliderPosition}>
          <div className="Packs-Content-Basic">
            <h2 className="Packs-Text-White">Bsico</h2>
            <div className="Packs-Content-Flex">
              <img src={basicoIconW} alt="basico icon" />
              <p className="Packs-Text-White">Bsico s no nome, o primeiro pacote da DVargas est longe de ser simplrio. Desenvolvemos seu projeto desde o incio junto com sua equipe, integrando e aplicando melhorias tecnologicas j testadas e em pleno funcionamento para seus clientes.<br/><br/>Sejam games, animaes de curta ou longa metragem, site ou at mesmo plataformas complexas, esse pacote engloba tudo!</p>
            </div>
            <h4 className="Packs-Text-White">Seu projeto digital ir sair do papel,<br/>ns damos vida ao que voc pensar! </h4>
            <Link to="/Pacote-Basico"><h4 className="Packs-Button" >Veja Alguns Exemplos</h4></Link>
          </div>

          <div className="Packs-Content-Advanced">
            <h2 className="Packs-Text-White">Avanado</h2>
            <div className="Packs-Content-Flex">
              <img src={avancadoIconW} alt="avancado icon" />
              <p className="Packs-Text-White">No s faremos o seu projeto como ajudaremos a levanta-lo do zero de acordo com as necessidades de seu pblico alvo. Utilizando estratgias de marketing desenvolvidas a partir de consultorias com nossos agentes, visamos o melhor desempenho para atingirmos o seu sucesso, lado a lado!<br/><br/>Se voc precisa tambm de suporte, esse pacote ir lhe atender.</p>
            </div>
            <h4 className="Packs-Text-White">No basta apenas criar um projeto,<br/>Precisamos fazer ele vingar!</h4>
            <Link to="/Pacote-Avancado"><h4 className="Packs-Button Packs-Dark-Red-Button">Veja Alguns Exemplos</h4></Link>
          </div>

          <div className="Packs-Content-Supreme">
            <h2 className="Packs-Text-White">Supremo</h2>
            <div className="Packs-Content-Flex">
              <img src={supremoIconW} alt="supremo icon" />
              <p className="Packs-Text-White">Se voc tem desejo de abrir um negcio digital ou criar um projeto, mas no tem nem mesmo a ideia do que ser, esse pacote  para voc!<br/><br/>Nossos especialistas faro a consultoria necessria para descobrir a rea e postura desejada. Durante a produo, treinaremos voc para gerir o seu negcio da melhor forma possvel a partir da entrega! E no  s isso, todo o marketing e suporte posterior  entrega esto INCLUSOS!</p>
            </div>
            <h4 className="Packs-Text-White">Do zero ao negcio ideal,<br/>com todo o suporte para brilhar!</h4>
            <Link to="/Pacote-Supremo"><h4 className="Packs-Button" >Veja Alguns Exemplos</h4></Link>
          </div>
        </div>
      </div>
    </div>
  )
}
