import "./packsPages.scss"

import Header from "../components/header/Header"

import ReactPlayer from "react-player";
import { Helmet } from 'react-helmet-async'
import video from './../assets/videos/video2.mp4'

export default function PacksAdvancedPage({refHeader}) {
  return (
    <div className="PacksPages">
      <Helmet>
        <title>Pacote Avanado</title>
        <meta name="description" content="A gente sabe que as vezes ter o projeto pronto nem sempre  tudo o que deve ser feito para levar ele ao sucesso..."/>
        <link rel="canonical" href="/Pacote-Avancado" />
      </Helmet>

      <Header title="Nossos Pacotes" refHeader={refHeader}/>
      <h1>Pacote Avanado</h1>
      <div className="PacksPages-Content">
        <ReactPlayer url={video} playing={false} width="80%" height="80%" pip={true} controls={true} className="PacksPages-Video"/>

        <h2>Legenda</h2>

        <p>A gente sabe que as vezes ter o projeto pronto nem sempre  tudo o que deve ser feito para levar ele ao sucesso, uma parte muito importante de toda boa empreitada rumo as estrelas  o Marketing/Atendimento/Suporte ou melhor, toda a estratgia de Marketing e  por isso que a DVargas criou o pacote avanado onde no s criaremos o seu projeto como tambm lhe daremos o suporte posterior com assistncia tcnicas e escalabilidade. Voc tem um sonho? Quer levar sua empresa at o ponto X? Ns vamos junto com vocs nessa caminhada! <br /><br />Ok, mas como funciona? <br /><br />Ao passo que todo o perodo de desenvolvimento  feito nossos especialistas montam o que vem a ser a estratgia de combate do seu projeto diante do mercado, traando o que dever ser feito com o produto em questo. O valor no  orado junto com o projeto no se preocupe, apenas a consultoria mensal e mo de obra esto includas na acessria em especifico.  Durante 2 meses de analise ns estudaremos o ponto central do problema pelo qual seu projeto existe e veio resolver aumentando suas chances de sucesso em 12% a cada ms de analise. <br /><br /> Quer saber mais? Entre em contato pelo boto abaixo, at daqui a pouco, tchau tchau.</p>
        <a href="/#Contato"><h4 className="PacksPages-Button" >Contato</h4></a>
      </div>  
    </div>
  )
}
